#app-lang {
    text-align: center;
    margin: var(--margin);
    margin-bottom: 0;
    position: absolute;

}

#App-lang-nextButton {
    right: 0;
    position: absolute
}

#app-lang-chimple-logo {
    text-align: center;
    width: auto;
    height: 25vh;
}

#app-lang-element {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#app-lang-text {
    font-size: var(--text-size);
    padding-top: 4vh;
    color: var(--text-color);
    margin-bottom: 2vh;
}


.app-container {
    position: relative;
    text-align: center;
}

.skip-button {
    justify-content: space-between;
    padding-left: min(10px, 2vw);
    right: 0;
    color: #05342f;
    font-size: var(--text-size);
    height: 7vh;
    width: clamp(95px, 10vh, 110px);
    height: clamp(40px, 8vh, 45px);
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 64px;
}

.navigation-dots {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;

}

#text {
    padding-top: 6vh;
}

.dot {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0 5px;
    border-radius: 50%;
    background-color: #ccc;
    cursor: pointer;
}

#header-text1 {
    font-size: clamp(18px, 4vh, 20vh);
    font-weight: bold;
    color: #FFBA00;
}

#header-text2 {
    font-size: clamp(18px, 4vh, 20vh);
    font-weight: bold;
    color: #88C327;
}

#header-text3 {
    font-size: clamp(18px, 4vh, 20vh);
    font-weight: bold;
    color: #FF5500;
}

.dot.active {
    background-color: #007bff;
}

.intro-text {
    display: flex;
    flex-direction: column;
    font-size: var(--text-size);
    color: var(--text-color);
    align-content: center;
}

.intro-text2 {
    line-height: 5vh;
}

.skip-button {
    text-align: center;
    background-color: #CCCCCC;
}

.skip-next {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#intro-logo {
    height: 54vh;
    position: sticky;
}

#login {
    width: 60vw;
    height: 57vh;
}

#profile-logo {
    width: 35vw;
    height: 55vh;
}

#advance-home-logo {
    width: 37vw;
    height: auto;
}